body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  display: grid;
  place-items: center;
  max-height: 100vh;
  background: #1A2A3A;  
  color: #FFF; 
  scroll-behavior: smooth;
}

.blob {
  max-height:25vh;
  width:90vw;
}
.blob path {
 -webkit-animation: blob 7s linear  infinite;
         animation: blob 7s linear  infinite;
