.wave {
  position: relative;
  /* display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; */
  min-height: 45vh;
  background-color: #f6f9fc;
  font-family: Roboto;
  overflow: hidden;
  

}

  .wave:before,
  .wave:after {
    content: "";
    position: absolute;
    left: 50%;
    top: 65%;
    min-width: 310vw;
    min-height: 300vw;
    background-color: #f49af6;
    -webkit-animation-name: rotate;
            animation-name: rotate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
  }
  
  .wave:before {
    border-radius: 45%;
    -webkit-animation-duration: 10s;
            animation-duration: 10s;
  }
  
  .wave :after {
    opacity: .5;
    border-radius: 47%;
    -webkit-animation-duration: 10s;
            animation-duration: 10s;
  }

@keyframes rotate {
  0% {-webkit-transform: translate(-50%, 0) rotateZ(0deg);transform: translate(-50%, 0) rotateZ(0deg);}
  50% {-webkit-transform: translate(-50%, -2%) rotateZ(180deg);transform: translate(-50%, -2%) rotateZ(180deg);}
  100% {-webkit-transform: translate(-50%, 0%) rotateZ(360deg);transform: translate(-50%, 0%) rotateZ(360deg);}
}
@-webkit-keyframes rotate {
  0% {-webkit-transform: translate(-50%, 0) rotateZ(0deg);transform: translate(-50%, 0) rotateZ(0deg);}
  50% {-webkit-transform: translate(-50%, -2%) rotateZ(180deg);transform: translate(-50%, -2%) rotateZ(180deg);}
  100% {-webkit-transform: translate(-50%, 0%) rotateZ(360deg);transform: translate(-50%, 0%) rotateZ(360deg);}
}
@keyframes rotate { 
  0% {-webkit-transform: translate(-50%, 0) rotateZ(0deg);transform: translate(-50%, 0) rotateZ(0deg);}
  50% {-webkit-transform: translate(-50%, -2%) rotateZ(180deg);transform: translate(-50%, -2%) rotateZ(180deg);}
  100% {-webkit-transform: translate(-50%, 0%) rotateZ(360deg);transform: translate(-50%, 0%) rotateZ(360deg);}
}
